<script setup>
import {onMounted, ref} from "vue"
import {ElMessage} from "element-plus";
import {store} from "@/store";
import {CommentOutlined, LikeOutlined, ReadOutlined, ShareAltOutlined} from "@ant-design/icons-vue"
import requestService from "@/utils/request";
import {getLocalStorageItem, setLocalStorageItem} from "@/utils/data";
import router from "@/router";
import {notification} from "ant-design-vue";

const token = ref("")

const imageUrl = ref("")

const upload = ref(true)

const beforeBackgroundUpload = function (rawFile) {
  token.value = getLocalStorageItem("token");
  if (rawFile.type !== 'image/jpeg' && rawFile.type !== 'image/png') {
    ElMessage.error('背景只允许JPG或PNG格式!');
    return false;
  } else if (rawFile.size / 1024 / 1024 > 10) {
    ElMessage.error('上传的背景图片文件大小不能超过10MB!');
    return false;
  }
  return true;
}

const handleBackgroundSuccess = async function (res, upload) {
  if (res.code === 1001) {
    ElMessage({
      type: 'success',
      message: '上传成功'
    })
    store.userInfo.bgImageUrl = res.data.filePath


    requestService.post("/user/changeBg", res.data.filePath).then((result) => {
      if (result.data.code === 4001) {
        localStorage.removeItem("token")
        setLocalStorageItem("token", result.data.data, 5)
        requestService.get('/user/').then((r) => {
          if (r.data.code === 2001) {
            localStorage.removeItem("userInfo")
            setLocalStorageItem("userInfo", r.data.data, 5)
            store.userInfo = r.data.data.userInfo
            store.followers = r.data.data.followers
            store.followings = r.data.data.followings
            store.isLogin = true

          }
        })

        ElMessage({
          type: 'success',
          message: '背景修改成功'
        })

      }
    })

  } else {
    ElMessage({
      type: 'warning',
      message: res.msg
    })
  }
}

const uploadRef = ref();

function changeBg() {
  return new Promise((resolve, reject) => {
    uploadRef.value.submit();
    resolve()
  })
}

const followersCount = ref(0);
const followingsCount = ref(0);

const getFollowsCount = () => {
  followersCount.value = store.followers.length;
  followingsCount.value = store.followings.length;
}

onMounted(() => {
  getFollowsCount();
  getFeed();
})

const ellipsis = ref(true);

const iconColor = ref("#000")

const changeColor = () => {
  iconColor.value = iconColor.value === '#000' ? "#de1c31" : "#000"
}

const getFeed = () => {
  requestService.get("/feed/getFeedByUserId/" + store.userInfo.userId).then((res) => {
    if (res.data.code === 2001) {
      feed.value = res.data.data;
    }
  })
}

const toFeedDetail = (feedId) => {
  router.push({path: '/postDetail', query: {feedId: feedId}})
}

const userInfo = ref({
  userName: "",
  userId: "",
  nickName: "",
  avatar: "",
})

const feed = ref([]);

const loading = ref(true);

const likeFeed = (item) => {
  requestService.post("/like",
      {
        feedId: item.feedId,
        feedUserId: item.userId
      },
      {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then((res) => {
    if (res.data.code === 3001) {
      openNotificationWithIcon('success', res.data.msg)
      item.likedUserIds.push(store.userInfo.userId)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}
const unLikeFeed = (item) => {
  requestService.delete("/like",
      {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'},
        data: {
          feedId: item.feedId,
          feedUserId: item.userId
        }
      }
  ).then((res) => {
    if (res.data.code === 5001) {
      openNotificationWithIcon('success', res.data.msg)
      // 将用户id从feed的likedUserIds中删除
      item.likedUserIds.splice(item.likedUserIds.indexOf(store.userInfo.userId), 1)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}

const openNotificationWithIcon = (type, title, description) => {
  notification[type]({
    message: title,
    description: description,
    duration: 2
  });
};
</script>

<template>
  <div class="user-display">
    <div class="user-background">
      <el-image class="bg-image" :src="store.userInfo.bgImageUrl" fit="cover"/>
    </div>

    <a-avatar :style="{marginTop:'-30px',marginLeft:'20px'}" :src="store.userInfo.avatar" :size="100"/>

    <div class="nickname">{{ store.userInfo.nickName }}</div>

    <div class="msg">
      <div><span>粉丝&nbsp;&nbsp;</span>{{ followersCount }}</div>
      <div><span>关注&nbsp;&nbsp;</span>{{ followingsCount }}</div>
    </div>

    <div>
      <el-upload class="background-uploader"
                 ref="uploadRef"
                 :show-upload-list="false"
                 action="/upload"
                 :data="{'operationCode':3}"
                 :headers="{'Authorization':token}"
                 :show-file-list="false"
                 :before-upload="beforeBackgroundUpload"
                 :on-success="handleBackgroundSuccess">
        <a-button class="upload-btn" type="primary" shape="round" @click="changeBg">更换背景</a-button>
      </el-upload>
    </div>


    <div class="desc">
      <ReadOutlined class="desc-icon"/>
      <a-typography-text>
        <div class="desc-text">
          {{ store.userInfo.description }}
        </div>
      </a-typography-text>
    </div>
  </div>

  <div :style="{marginTop:'20px',letterSpacing:'1px'}">
    全部动态({{ feed.length }})
  </div>

  <div :style="{marginTop:'20px'}" style="white-space: pre-wrap;">
    <a-card v-for="(item,index) in feed" :key="index" class="post" size="default">
      <a-card-meta>
        <template #title>
          <div class="post-nickname">{{ item.userNickName }}</div>
        </template>

        <template #description>
          <div class="post-createtime">{{ item.createTime }}</div>
        </template>

        <template #avatar>
          <a-avatar :size="60" :src="item.userAvatar"></a-avatar>
        </template>
      </a-card-meta>

      <div class="post-content">
        <a-typography-paragraph :ellipsis="ellipsis ? { rows: 5, expandable: true } : false"
                                :style="{lineHeight: '25px',fontSize:'16px'}" :content="item.content">
          <template #ellipsisSymbol>
            <span :style="{color:'#de1c31'}">展开</span>
          </template>
        </a-typography-paragraph>
      </div>


      <a-image
          :src="item.imageUrl"
      />

      <template #actions>
        <a-button>
          <template #icon>
            <ShareAltOutlined key="forward"/>
          </template>
        </a-button>

        <a-button @click="toFeedDetail(item.feedId)">
          <template #icon>
            <CommentOutlined key="comment"/>
          </template>
          {{ item.comments.length }}
        </a-button>

        <a-button @click="unLikeFeed(item)" v-if="item.likedUserIds.includes(store.userInfo.userId)">
          <template #icon>
            <LikeOutlined style="color: #de1c31" key="like"/>
          </template>
          {{ item.likedUserIds.length }}
        </a-button>
        <a-button @click="likeFeed(item)" v-else>
          <template #icon>
            <LikeOutlined key="like"/>
          </template>
          {{ item.likedUserIds.length }}
        </a-button>

      </template>
    </a-card>

  </div>
</template>

<style scoped>

.user-display {
  margin-top: 20px;
  background-color: white;
  border-radius: 5px;
  height: fit-content;
  padding-bottom: 20px;
  width: 100%;
}

.user-background {
  width: 100%;
  height: 350px;
  background-color: #de1c31;

}

.bg-image {
  width: 100%;
  height: 100%;
}

.nickname {
  font-size: 18px;
  font-weight: bold;
  margin-left: 130px;
  margin-top: -60px;
}

.msg {
  display: flex;
  justify-content: space-between;
  margin-left: 130px;
  margin-top: 10px;
  width: 150px;
  height: 20px;
}

.msg div {
  float: left;
  margin-right: 10px;
}

.msg div span {
  color: #c2c2c2;
}

.desc {
  min-width: 600px;
  max-width: 600px;
  display: flex;
  justify-content: space-between;
  width: 600px;
  height: fit-content;
  max-height: 100px;
  margin-top: 25px;
  margin-left: 15px;
}

.desc-icon {
  color: rgb(131, 130, 130);
  font-size: 17px;
  margin-top: 3px;
}

.desc-text {
  font-size: 13px;
  color: rgb(131, 130, 130);
  margin-left: 20px;
  box-sizing: content-box;
}

.upload-btn {
  position: absolute;
  background-color: white;
  color: #de1c31;
  box-shadow: none;
  border: 1px #de1c31 solid;
  margin-left: 1150px;
  margin-top: -100px;
  margin-bottom: -10px;
}

.upload-btn:hover {
  background-color: white;
  color: rgba(222, 28, 49, 0.48);
}


.post {
  margin-bottom: 20px;
}

.post button {
  border: none;
  box-shadow: none;
}

.post button:hover {
  color: #de1c31;
}

/*消除按钮点击样式*/
:global(.ant-wave) {
  display: none !important;
}

.post-nickname {
  font-size: 19px;
  min-width: 100px;
}

.post-createtime {
  font-size: 15px;
}

.post-content {
  margin: 20px 0;
}
</style>